import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function taxBurdenTtm(tableName: string) {
  const title = "Tax Burden (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["taxBurdenTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Portion of earnings kept after taxes.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about tax burden or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.taxBurdenTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={(info.row.original.financialData.companyKeyMetrics?.data?.taxBurdenTtm ?? 0) * 100}
        />
      )
    },
  })
}

export { taxBurdenTtm }
